"use client";
import { useEffect, useState, useRef } from "react";
import { Pencil, Trash2, X } from "lucide-react";
import { Button } from "../ui/button";
import { useRouter } from "next/navigation";
import clsx from "clsx";
import Image from "next/image";

// Mock data for students
const ALL_STUDENTS = [
  { id: 1, name: "ដារ៉ែល ស្ទូវ៉ាត", avatar: "/avatars/1.png" },
  { id: 2, name: "Marc Atenson", avatar: "/avatars/2.png" },
  { id: 3, name: "Orion Blake", avatar: "/avatars/3.png" },
  { id: 4, name: "Susan Drake", avatar: "/avatars/4.png" },
];

const MOCK_CLASSROOM = {
  id: 1,
  name: "ថ្នាក់ 12 A",
  year: "2024-2025",
  students: [
    { id: 1, name: "ដារ៉ែល ស្ទូវ៉ាត", avatar: "/avatars/1.png" },
    { id: 2, name: "Marc Atenson", avatar: "/avatars/2.png" },
    { id: 4, name: "Susan Drake", avatar: "/avatars/4.png" },
  ],
};

export default function ClassroomDetail({ classroomId }: { classroomId: string }) {
  const [classroom, setClassroom] = useState(MOCK_CLASSROOM);
  const [showAddStudent, setShowAddStudent] = useState(false);
  const [selectedStudents, setSelectedStudents] = useState<number[]>([]);
  const popupRef = useRef<HTMLDivElement | null>(null);
  const router = useRouter();

  // For popup: close on outside click and Escape key
  useEffect(() => {
    function handleClickOutside(e: MouseEvent) {
      if (popupRef.current && !popupRef.current.contains(e.target as Node)) {
        setShowAddStudent(false);
      }
    }
    function handleEscape(e: KeyboardEvent) {
      if (e.key === "Escape") setShowAddStudent(false);
    }
    if (showAddStudent) {
      document.addEventListener("mousedown", handleClickOutside);
      document.addEventListener("keydown", handleEscape);
    }
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.removeEventListener("keydown", handleEscape);
    };
  }, [showAddStudent]);

  // Remove student from classroom (UI only)
  const handleRemoveStudent = (id: number) => {
    setClassroom((prev) => ({
      ...prev,
      students: prev.students.filter((s) => s.id !== id),
    }));
  };

  // Open edit student page
  const handleEditStudent = (id: number) => {
    router.push(`/school/student/${id}/editStudents`);
  };

  // Add students to classroom
  const handleAddStudents = () => {
    const newStudents = ALL_STUDENTS.filter(
      (s) => selectedStudents.includes(s.id) && !classroom.students.some(cs => cs.id === s.id)
    );
    setClassroom((prev) => ({
      ...prev,
      students: [...prev.students, ...newStudents],
    }));
    setShowAddStudent(false);
    setSelectedStudents([]);
  };

  // Toggle student selection in popup
  const toggleStudent = (id: number) => {
    setSelectedStudents((prev) =>
      prev.includes(id) ? prev.filter((sid) => sid !== id) : [...prev, id]
    );
  };

  // Students available to add (not already in classroom)
  const availableStudents = ALL_STUDENTS.filter(
    (s) => !classroom.students.some(cs => cs.id === s.id)
  );

  return (
    <div className="space-y-4 relative max-w-5xl mx-auto mt-8">
      {/* Header */}
      <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4 px-2">
        <div className="flex items-center gap-3">
          <div
            className={clsx(
              "w-10 h-10 rounded-full text-white flex items-center justify-center font-bold bg-green-600"
            )}
          >
            {classroom.name.charAt(0)}
          </div>
          <div>
            <div className="text-base font-semibold">{classroom.name}</div>
            <div className="text-xs text-gray-500">{classroom.year}</div>
          </div>
        </div>
        <Button
          className="bg-[#25388C] hover:bg-[#1e2e6d] flex items-center gap-2 px-4 py-2 rounded-lg text-white font-medium"
          onClick={() => setShowAddStudent(true)}
        >
          <Image src="/icons/plus.svg" alt="Add" width={18} height={18} />
          <span>បន្ថែមសិស្សថ្មី</span>
        </Button>
      </div>

      {/* Table */}
      <div className="overflow-x-auto rounded-xl border border-gray-200 shadow-sm bg-white">
        <table className="min-w-full text-sm text-left">
          <thead className="bg-gray-50 text-gray-600 uppercase text-xs">
            <tr>
              <th className="px-6 py-4">ឈ្មោះ</th>
              <th className="px-6 py-4">សកម្មភាព</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-100 bg-white">
            {classroom.students.map((student) => (
              <tr key={student.id}>
                <td className="px-6 py-4 flex items-center gap-3">
                  <div className="w-8 h-8 rounded-full bg-gray-200 overflow-hidden flex items-center justify-center">
                    {student.avatar ? (
                      <img src={student.avatar} alt={student.name} className="w-full h-full object-cover" />
                    ) : (
                      <span className="text-lg font-bold text-gray-500">{student.name.charAt(0)}</span>
                    )}
                  </div>
                  <div className="text-sm font-medium">{student.name}</div>
                </td>
                <td className="px-6 py-4 flex gap-2">
                  <button
                    className="text-blue-500 hover:text-blue-700"
                    onClick={() => handleEditStudent(student.id)}
                    title="Edit"
                  >
                    <Pencil size={18} />
                  </button>
                  <button
                    className="text-red-500 hover:text-red-700"
                    onClick={() => handleRemoveStudent(student.id)}
                    title="Remove"
                  >
                    <Trash2 size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Add Student Popup */}
      {showAddStudent && (
        <div className="fixed inset-0 z-50 bg-black/30 flex items-center justify-center" style={{ backdropFilter: "blur(2px)" }}>
          <div
            ref={popupRef}
            className="bg-white rounded-xl p-6 shadow-lg w-full max-w-md flex flex-col gap-4 relative"
          >
            <button
              className="absolute top-4 right-4 text-gray-400 hover:text-black"
              onClick={() => setShowAddStudent(false)}
              aria-label="Close"
            >
              <X size={22} />
            </button>
            <h2 className="text-lg font-bold text-center mb-2">បញ្ចូលសិស្ស</h2>
            {/* Selected chips */}
            <div className="flex flex-wrap gap-2 mb-4">
              {selectedStudents.map((id) => {
                const s = ALL_STUDENTS.find((stu) => stu.id === id);
                if (!s) return null;
                return (
                  <span key={id} className="flex items-center gap-1 bg-gray-100 rounded-full px-3 py-1 text-sm">
                    {s.avatar && (
                      <img src={s.avatar} alt={s.name} className="w-6 h-6 rounded-full object-cover" />
                    )}
                    {s.name}
                    <button
                      className="ml-1 text-gray-400 hover:text-red-500"
                      onClick={() => toggleStudent(id)}
                      type="button"
                    >
                      <X size={14} />
                    </button>
                  </span>
                );
              })}
            </div>
            {/* Student list */}
            <div className="flex flex-col gap-2 max-h-56 overflow-y-auto">
              {availableStudents.length === 0 && (
                <div className="text-center text-gray-400">No students available</div>
              )}
              {availableStudents.map((student) => (
                <button
                  key={student.id}
                  className={`flex items-center gap-3 px-3 py-2 rounded-lg border ${
                    selectedStudents.includes(student.id)
                      ? "border-[#25388C] bg-[#F0F3FF]"
                      : "border-gray-200 bg-white"
                  } hover:bg-[#F6F8FB] transition`}
                  onClick={() => toggleStudent(student.id)}
                  type="button"
                >
                  <span
                    className={`w-6 h-6 flex items-center justify-center rounded-full border ${
                      selectedStudents.includes(student.id)
                        ? "bg-[#25388C] border-[#25388C] text-white"
                        : "bg-white border-gray-300 text-[#25388C]"
                    }`}
                  >
                    {selectedStudents.includes(student.id) ? (
                      <svg width="16" height="16" fill="none" viewBox="0 0 16 16">
                        <path d="M4 8.5l3 3 5-5" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                      </svg>
                    ) : (
                      <span className="block w-3 h-3 rounded-full border"></span>
                    )}
                  </span>
                  {student.avatar && (
                    <img src={student.avatar} alt={student.name} className="w-7 h-7 rounded-full object-cover" />
                  )}
                  <span className="font-medium">{student.name}</span>
                </button>
              ))}
            </div>
            <Button
              className="bg-[#25388C] hover:bg-[#1e2e6d] w-full mt-4"
              onClick={handleAddStudents}
              disabled={selectedStudents.length === 0}
            >
              បញ្ចូល
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}